
































































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static: '',
			serverinf: {},
			date: '',
			time: '',
			y: "",
			m: '',
			d: '',
			adress: ''
		}
	},
	onLoad() {
		this.static = ut.static;
		this.req_getdefaddress();
		this.serverinf = wx.getStorageSync('serverinf');
		console.log(JSON.stringify(this.serverinf))
		wx.setStorageSync('serverinf', '');
	},
	onShow() {
		if (wx.getStorageSync('adress')) {
			this.adress = wx.getStorageSync('adress');
			wx.setStorageSync('adress', '');
		}
	},
	methods: {
		go_mine_adress() {
			wx.navigateTo({
				url: '../mine/adress?src=pay'
			})
		},
		req_unifiedOrder() {
			if (!this.adress) {
				ut.totast('请选择地址');
				return;
			}
			if (!this.date) {
				ut.totast('请选择日期');
				return;
			}
			if (!this.time) {
				ut.totast('请选择时间');
				return;
			}
			wx.requestPayment({
				'timeStamp': '1547896015',
				'nonceStr': 'dVG4QZFIiIejYJS6',
				'package': 'prepay_id=wx19190655088975f108ef9d5d2143777455',
				'paySign': 'E362EE860A1A0923C6F7D79805F6005473F0CB4F722BA037B7D201603231B09E',
				'signType': 'HMAC-SHA256',
				success: function(res) {
					
				},
				'fail': function(res) {
					console.log(res)
				},
				'complete': function(res) {
					wx.redirectTo({
						url: '../order/checkOrder?type=1'
					})
				}
			});
			return
			ut.request({
				data: {
					"addressId": this.adress.id,
					"makeTime": this.date + this.time,
					"remark": '',
					"serviceId": this.serverinf.id
				},
				url: "service/order/unifiedOrder",
				c: true
			}).then(data => {
				wx.requestPayment({
					'timeStamp': '1547896015',
					'nonceStr': 'dVG4QZFIiIejYJS6',
					'package': 'prepay_id=wx19190655088975f108ef9d5d2143777455',
					'paySign': 'E362EE860A1A0923C6F7D79805F6005473F0CB4F722BA037B7D201603231B09E',
					'signType': 'HMAC-SHA256',
					success: function(res) {
						
					},
					'fail': function(res) {
						console.log(res)
					},
					'complete': function(res) {
						wx.redirectTo({
							url: '../order/checkOrder?type=1'
						})
					}
				});
			})
		},
		timeChange: function(e) {
			this.time = e.detail.value;
		},
		dateChange: function(e) {
			this.date = e.detail.value;
			const date = this.date.split('-');
			this.y = date[0];
			this.m = date[1];
			this.d = date[2];
		},
		req_getdefaddress() {
			ut.request({
				url: "address/getdefaddress"
			}).then(data => {
				this.adress = data;
			})
		}
	}
}



